/* 
[除了bootstrap的reboot之外，不要對純HTML TAG 下樣式]

例如: 
不要
label{font-size:12px} 

建議
register__label{font-size:12px};
主要是如果未來要把部分元件抽到別站使用的話，會被別站的 global 樣式影響

若發現某些樣式是通用的話，可採取2個作法
(1) 命名讓他廣泛一點 register__label -> form__label
(2) 將他做成 mixin 在各元件中 include 使用

@mixin label_template() {
    $font-size:12px;
}

.register__label{
    @include label_template();
}

.login__label{
    @include label_template();
}

[命名規則]
．如果需要額外寫到 class的話請幫我用 BEM 的命名方式 http://getbem.com/naming/，
．採用BEM後，避免還過度 nesting 的寫法，
．樣式不要下在 id 上
*/

$font-size-sm: 14px;
$font-size-lg-md: 24px;
$font-size-lg-sm: 20px;
$font-size-lg: 24px;
$bg-color: #edf3e9;
$custom-select-width: 192px;
$custom-radio-inside-circle-wh: 14px;
$primary-color:#00a94f;

.cathay-main, main{
  padding-top:50px;
  @media (min-width: 992px) {
    padding-top:0px;
  }
}

.cursor-pointer{
  cursor:pointer;
}
.events-none{
  pointer-events:none;
}
.icon__eye{
  display: block;
  background-size: cover;
  min-width: 24px;
  width: 24px;
  height:24px; 
  // top: 1px
  background-size:24px 24px;
  background-repeat: no-repeat; 
  background-image: url(data:image/svg+xml;base64,);
 &.icon__eye--active{
  top: -0.5px;
  background-image: url(data:image/svg+xml;base64,);
  
  
  }
}
.form__password{
  i{
    padding:20px;
    top:0;
    right: 0;
    background-position: center;
    z-index: 1000;
  }
}
.form-group.is-invalid .invalid-feedback, .form-group.is-invalid .invalid-tooltip, .was-validated .form-group:invalid .invalid-feedback, .was-validated .form-group:invalid .invalid-tooltip{
  display:block;
}

input::-ms-clear, input::-ms-reveal {
  display: none;
}
::-ms-reveal {
  display: none;
}


.register__invalid {
  input {
    border-color: $wrong !important;
  }
  .custom-control-label {
    &:before{
      border-color: $wrong !important;
    }
    &:after{
    }
  }
  .invalid-feedback {
    // display: block !important;
    // color: $wrong;
    // margin-top: .25rem;
  }
}


.register__input_icon_position {
  top: 0.5rem;
  right: 0.5rem;
}

.register__text__sm {
  font-size: $font-size-sm;
}



.register__text__lg {
  font-size: $font-size-lg;
}

.register__area {
  .custom-select {
    max-width: $custom-select-width;
    &:checked {
      transition: 0.15s ease-in-out;
    }
  }
}

.form-control:disabled {
  color: $gray-999;
  -webkit-text-fill-color: $gray-999;
}

.custom-radio .custom-control-input:checked ~ .custom-control-label:before {
  background: #fff;
}

.custom-radio .custom-control-input:checked ~ .custom-control-label:after {
  background: $green;
  border-radius: 100%;
  width: $custom-radio-inside-circle-wh;
  height: $custom-radio-inside-circle-wh;
  margin-top: 5px;
  margin-left: 5px;
}



.mt-n1px{
  margin-top: -1px;
}



.btn-icon{
  &:hover,&.active{
    opacity:.8
  }
}

.event-none{
  pointer-events: none;
}


.color-green {
  color: #00a94f
}

.color-orange {
  color: #de7900
}

.color-grey {
  color: #999
}

.color-important {
  color: #f9556f
}


ul,ol{
  &.list-green{
    list-style: none;
    padding: 0 0 0 1.5rem;
    margin: 0 0 1.5rem 0;
    
  }
}

ul.list-green{
  li{
    position: relative;
    &:before{
      content:'';
      position: absolute;
      display: block;
      width:5px;
      height: 5px;
      top:.6rem;
      left: -1rem;
      border-radius: 50%;
      background-color: #00A94F;
    }

    +li{
      margin-top: .5rem;
    }
  }
}

.mt-lg-56{
  
  @media (min-width: 992px) {
    margin-top: 56px !important;
  }
}

.w-100p{
  width: 100% !important;
}
.w-md-auto{
  @media (min-width: 768px) {
    width: auto !important;
  }
}
.w-md-240{
  @media (min-width: 768px) {
    width:240px !important;
  }
}