@import "../../mixins";

.btn{
	@include btn('m');
	@include btn_size('m');
	@include btn_color();
	// +.btn{
	// 	margin-left: 10px;
	// }
}
.btn--s{
	@include btn_size('s');
}
.btn--xs{
	@include btn_size('xs');
}
.btn--l{
	@include btn_size('lg');
}

//color
.btn--white{
	@include btn_color('white');
    &.btn--arrow-right{
        @include btn_icon('arrow-right',#888,8px,12px,2px,2px,-18px);
    }

    &:hover{
        &.btn--arrow-right{
            @include btn_icon('arrow-right',#00a94f,8px,12px,2px,2px,-18px);
        }
    }
}

.btn--orange{
	@include btn_color('orange');
    &.btn--arrow-right{
        @include btn_icon('arrow-right',#fff,8px,12px,2px,2px,-18px);
    }
}

.btn--green{
	@include btn_color('green');
    &.btn--arrow-right{
        @include btn_icon('arrow-right',#fff,8px,12px,2px,2px,-18px);
    }
}
